@font-face {
  font-family: 'Epilogue_Title';
  src: url(/my-test-project/static/media/Epilogue-Medium.ef359a4e.ttf);
}
@font-face {
  font-family: 'Epilogue_Sub';
  src: url(/my-test-project/static/media/Epilogue-Black.bb7e1465.ttf);
}
body {
  margin: 0;
  padding: 0;
  background-color: rgb(14, 12, 32);
}
* {
  font-family: 'Epilogue_Title';
  font-size: 18px;
  color:rgb(213, 213, 213);
}
.loading svg polyline {
    fill: none;
    stroke-width: 3;
    stroke-linecap: round;
    stroke-linejoin: round;
  }
.loading {
    position: absolute;
    left: calc(50% - 32px);
    top: calc(50vh - 24px)
}
  
  .loading svg polyline#back {
    fill: none;
    stroke: #6e4dff33;
  }
  
  .loading svg polyline#front {
    fill: none;
    stroke: #9784ff;
    stroke-dasharray: 48, 144;
    stroke-dashoffset: 192;
    -webkit-animation: dash_682 1.4s linear infinite;
            animation: dash_682 1.4s linear infinite;
  }
  
  @-webkit-keyframes dash_682 {
    72.5% {
      opacity: 0;
    }
  
    to {
      stroke-dashoffset: 0;
    }
  }
  
  @keyframes dash_682 {
    72.5% {
      opacity: 0;
    }
  
    to {
      stroke-dashoffset: 0;
    }
  }
  
